.portfolio {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
  
  }
  /* heading */
  .portfolio span:nth-of-type(1) {
    color: var(--black);
    font-size: 3rem;
    font-weight: bold;
  }
  
  .portfolio span:nth-of-type(2) {
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
  }
  
  /* slider */
  .portfolio .swiper{
      overflow: visible!important;
  }
  
  .portfolio-slider {
    margin-top: 2rem;
    width: 100%;
  }
  
  .portfolio-slider .swiper-slide {
    width: 20rem;
  }
  .portfolio img {
    width: 30rem;
    filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
    border-radius: 19px;
  }

  @media screen and (max-width: 720px) {
    .portfolio {
        flex-direction: column;
        gap: 7rem;
        margin-bottom: 13rem;
      }
      .portfolio span:nth-of-type(1) {
        color: var(--black);
        font-size: 3rem;
        font-weight: bold;
        margin-bottom: -7rem;
        text-align: center;
      }
      
      .portfolio span:nth-of-type(2) {
        color: var(--orange);
        font-size: 2.5rem;
        font-weight: bold;
        text-align: center;
        margin-bottom: -7rem;

      }
}