/* All Div Styling */
.Intro {
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}

/* Left Side Styling */
.i-left {
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}

.i-name {
    display: flex;
    flex-direction: column;
}

.i-name > :nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 4rem;
}

.i-name > :nth-child(2) {
    color: var(--orange);
    font-weight: bold;
    font-size: 4rem;
}

.i-name > :nth-child(3) {
    font-weight: 100;
    font-size: 25px;
    color: var(--grey);
    margin-top: 10px;
}

.i-button {
    width: 6rem;
    height: 2rem;
    margin-top: 1rem;
}

.i-icons {
    margin-top: 3rem;
    display: flex;
    gap: 1.3rem;
}

.i-icons > * {
    transform: scale(0.5);
}

.i-icons > *:hover {
    cursor: pointer;
}

/* Right Side Styling */
.i-right {
    flex: 1;
    position: relative;
}

.i-right > * {
    position: absolute;
    z-index: 1;
}

.i-right > :nth-child(1) {
    transform: scale(0.86);
    left: 15%;
    top: -9%;
}

.i-right > :nth-child(2) {
    transform: scale(0.90);
    left: 3rem;
    top: -4.6%;
}

.i-right > :nth-child(3) {
    transform: scale(1.8);
    left: 35%;
    top: 9%;
}

.i-right > :nth-child(4) {
    transform: scale(0.98);
    top: -9%;
    left: -10%;
    border-radius: 60%;
    padding: 0;
}

.blur {
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    filter: blur(100px);
    z-index: -9;
    top: 20%;
    left: 72%;
}

/* Responsive Design */
@media screen and (max-width: 720px) {
    .Intro {
        flex-direction: column;
        gap: 4rem;
        height: 55rem;
    }
    
    .i-right {
        transform: scale(0.8);
        left: -3rem;
    }

    .i-right .blur {
        display: none;
    }

    .floating-div:nth-of-type(1) {
        top: -7rem !important;
    }
}
